import * as React from 'react';
import * as ReactKonva from 'react-konva';

export interface Props {
  timeLeft: number
}

class Timer extends React.Component<Props, {seconds: number}>{

  fontWidth: number;
  canvas: HTMLCanvasElement;
  ctx: CanvasRenderingContext2D;
  interval: any;

  constructor(props: Props){
    super(props);
    this.state = {seconds: Math.floor(props.timeLeft/1000)};
  }

  componentDidMount() {
    let text = this.refs.text as any;
    this.canvas = text.getCanvas() as any;
    this.ctx = this.canvas.getContext('2d') as CanvasRenderingContext2D;
    this.fontWidth = this.canvas.width/3;

    this.interval = setInterval(
      () => this.tick(),
      1000
    );
  }

  render(){
    return (
      <ReactKonva.Text
      ref='text'
      x={0}
      y={0}
      text={this.getTimerText(this.state.seconds)}
      fontSize={this.fontWidth}
      fontFamily={'Calibri'}
      fill={this.state.seconds > 5 ? 'white' : 'red'}
      stroke={'black'}
      />
    );
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  };

  tick() {
    this.setState((prevState, props) => ({
      seconds: prevState.seconds - 1
    }));
  };

  getTimerText(secs: number): string{
    if(secs < 0){
      return '00 : 00';
    }
    
    var minutes: number = Math.floor(secs / 60);
    if (minutes === 0){
      var firstStr: string = '00';
    } else if (minutes < 10) {
      var firstStr: string = '0'+minutes.toString();
    } else {
      var firstStr: string = minutes.toString();
    }

    var seconds: number = (secs % 60);
    if (seconds === 0){
      var secStr: string = '00';
    } else if (secs < 10) {
      var secStr: string = '0'+seconds.toString();
    } else {
      var secStr: string = seconds.toString();
    }
    return  firstStr +' : '+ secStr; 
  }
}

export default Timer;


