import { playersAction } from '../actionTypes';
import { PlayerState } from '../../utils/objectTypes';
import * as constants from '../constants';

export function playersReducer(oldPlayers: PlayerState = {byName: {}, names: []}, action: playersAction): PlayerState {
    var newPlayers = {...oldPlayers};

    switch (action.type) {
        case constants.ADD_PLAYER:
        newPlayers.byName[action.player.name] = action.player;
        newPlayers.names = newPlayers.names.concat(action.player.name);
        return newPlayers;

        case constants.REMOVE_PLAYER:
        delete newPlayers.byName[action.name];
        newPlayers.names = newPlayers.names.filter(name => name === action.name);
        return newPlayers;

        case constants.MODIFY_PLAYER:
        newPlayers.byName[action.player.name] = action.player;
        return newPlayers;
        
        case constants.MODIFY_TAIL:
        var newPlayer = {...newPlayers[action.name], tail: action.tail};
        newPlayers[action.name] = newPlayer;
        return newPlayers;

        default: return oldPlayers;
            
    }
}


