import * as React from 'react';
import * as Konva from 'react-konva';

export interface Props {
  color: string;
  rect: {
    X: number,
    Y: number,
    Width: number,
    Height: number
  };
  key: string;
}

class TailComponent extends React.Component<Props, object>{
  constructor(props: Props){
    super(props);
  }

  shouldComponentUpdate(nextProps: Props, nextState: object){
    return false;
  }

  render(){
    return (
      <Konva.Rect
        x={this.props.rect.X}
        y={this.props.rect.Y}
        width={this.props.rect.Width}
        height={this.props.rect.Height}
        fill={this.props.color}
        shadowBlur={3}
        opacity={0.25}
      />
    );
  }
}

export default TailComponent;


