import * as constants from './constants'
import { Point, Player, Settings } from '../utils/objectTypes'

// Board

export interface createBoard {
    type: constants.CREATE_BOARD,
    dimension: Point
}

export interface claimFields {
    type: constants.CLAIM_FIELDS,
    coords: Point[],
    color: number
}

// Players

export interface addPlayer {
    type: constants.ADD_PLAYER,
    player: Player
}

export interface modifyPlayer {
    type: constants.MODIFY_PLAYER,
    player: Player
}

export interface modifyTail {
    type: constants.MODIFY_TAIL,
    tail: Point[],
    name: string
}

export interface removePlayer {
    type: constants.REMOVE_PLAYER,
    name: string
}

export interface setSettings {
    type: constants.SET_SETTINGS,
    settings: Settings
}

export type settingsAction = setSettings;
export type fieldsAction = claimFields | createBoard;
export type playersAction = addPlayer | removePlayer | modifyPlayer | modifyTail;
export type action = playersAction | fieldsAction | settingsAction;

export function createBoard(dimension: Point): createBoard {
    return {
        type: constants.CREATE_BOARD,
        dimension
    }
}

export function claimFields(coords: Point[], color: number): claimFields {
    return {
        type: constants.CLAIM_FIELDS,
        coords: coords,
        color: color
    }
}

export function addPlayer(player: Player): addPlayer {
    return {
        type: constants.ADD_PLAYER,
        player
    }
}

export function removePlayer(name: string): removePlayer {
    return {
        type: constants.REMOVE_PLAYER,
        name: name
    }
}

export function modifyPlayer(player: Player): modifyPlayer {
    return {
        type: constants.MODIFY_PLAYER,
        player
    }
}

export function modifyTail(name: string, tail: Point[]): modifyTail {
    return {
        type: constants.MODIFY_TAIL,
        name,
        tail
    }
}

export function setSettings(settings: Settings): setSettings {
    return {
        type: constants.SET_SETTINGS,
        settings
    }
}


