import * as React from 'react';

import { Point, GameState } from '../../utils/objectTypes'

export interface Props {
    state: GameState,
    onCreate: (coords: Point) => void,
    onClaim: (coords: Point[], color: number) => void,
}

function SettingsComponent({state, onCreate, onClaim}: Props) {
      var state: GameState = state;
    return (
      <div className="App">
      </div>
    );
}

export default SettingsComponent;


