import { settingsAction } from '../actionTypes';
import { Settings } from '../../utils/objectTypes';
import * as constants from '../constants';

export function settingsReducer(state: Settings = {optimized: false, endTime: 50000, startTime: 0}, action: settingsAction): Settings {

    switch (action.type) {
        case constants.SET_SETTINGS:
            return action.settings;
    }
    return state;
}


