import { fieldsAction } from '../actionTypes';
import { BoardField, FieldState, Point } from '../../utils/objectTypes';
import { getEmptyFields } from '../../utils/functions';
import * as constants from '../constants';

export function fieldsReducer(state: FieldState = {fields: [[]], dimension: {X:0, Y:0}, lastUpdatedCoords: [], startingTerritorySize: 0}, action: fieldsAction): FieldState {

    switch (action.type) {
        case constants.CREATE_BOARD:
            var newState = copyState(state);
            newState.fields = getEmptyFields(action.dimension);
            newState.dimension = action.dimension;
            return newState;

        case constants.CLAIM_FIELDS:
        if(action.coords.length === 0){ return state; }
            var newState = copyState(state);
            action.coords.forEach((coord: Point) => {
                if (fieldNotExists(newState.fields, coord)) return;
                newState.fields[coord.X][coord.Y] = {color: action.color}
                newState.lastUpdatedCoords.push(coord);                
            });
            return newState;
            
        default: return state;
    }
}

//helpers

function copyState(state: FieldState): FieldState{
        var newState = {...state};
        newState.fields = newState.fields.slice();
        newState.fields.forEach((row: BoardField[]) => {
            row = row.slice();
        });
        newState.lastUpdatedCoords = [];        
    return newState;    
}

function fieldNotExists(fields: BoardField[][], coord: Point): boolean{
    return fields[coord.X] === undefined || fields[coord.X][coord.Y] === undefined;
}


