import SettingsComponent from '../../ReactView/components/SettingsComponent';
import * as actions from '../actionTypes';
import { connect, Dispatch } from 'react-redux';

import { Point, GameState } from '../../utils/objectTypes'

export function mapStateToProps(state: GameState) {
  return {
      state
  }
}

export function mapDispatchToProps(dispatch: Dispatch<actions.action>) {
    return {
      onCreate: (coords: Point) => dispatch(actions.createBoard(coords)),
      onClaim: (coords: Point[], color: number) => dispatch(actions.claimFields(coords, color))
    }
  }

export default connect(mapStateToProps, mapDispatchToProps)(SettingsComponent);


