import * as React from 'react';
import * as ReactKonva from 'react-konva';
import * as Konva from 'konva';

export interface Props {
  color: string;
  rect: {
    X: number,
    Y: number,
    Width: number,
    Height: number
  };
  direction: string;
  speed: number;  
  optimized: boolean;
  key: string;
}

class PlayerComponent extends React.Component<Props, object>{
  
  private previousRect: {
    X: number,
    Y: number,
    Width: number,
    Height: number
  };

  constructor(props: Props) {
    super(props)
    this.previousRect = props.rect;
  }

  shouldComponentUpdate(nextProps: Readonly<Props>, nextState: object){
    if (nextProps.rect.X === this.props.rect.X && nextProps.rect.Y === this.props.rect.Y){
       return false;
     }
     return true;
  }

  componentWillUpdate(nextProps: Props, nextState: Props){
    this.previousRect = this.props.rect;
  }

    render(){
      let element;
      if(this.props.optimized){
        element = <ReactKonva.Rect
          ref="rect"
          x ={this.props.rect.X}
          y={this.props.rect.Y}
          width={this.props.rect.Width}
          height={this.props.rect.Height}
          fill={this.props.color}
          shadowBlur={7}
          opacity={1}
        />

        } else {
          element = <ReactKonva.Rect
          ref="rect"
          width={this.props.rect.Width}
          height={this.props.rect.Height}
          fill={this.props.color}
          shadowBlur={7}
          opacity={1}
        />
  
        }
      return (
        element
      );  
  }

  componentDidUpdate(){
    if(this.props.optimized){
      return;
    }
    
    const rect = this.refs.rect as any;

    rect.x = this.previousRect.X;
    rect.y = this.previousRect.Y;    
    rect.to({
      x: this.props.rect.X,
      y: this.props.rect.Y,
      duration: this.props.speed/1000,
      easing: Konva.Easings.Linear
    });
  }
}
export default PlayerComponent;


