import * as React from 'react';
import { createStore, Reducer, Store } from 'redux';
import { Provider } from 'react-redux';

import './Game.css';
import { registerGameController } from './GameLogic/GameController'
import GameBoard from './ReactView/components/GameBoard';
import ScoreBoard from './ReduxStore/containers/ScoreBoard';
import SettingsControl from './ReduxStore/containers/SettingsControl';

import { gameReducer } from './ReduxStore/reducers/combinedReducers';
import { GameState } from './utils/objectTypes';
import { initialState } from './utils/initialState'

class App extends React.Component {

  private store: Store<GameState>;

  componentWillMount() {
    this.store = createStore<GameState>(gameReducer as Reducer<GameState>, initialState);    
    registerGameController(this.store);
  }

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h2>Welcome to Color Wars</h2>
        </div>
        <div className="Board">
          <div className="ScoreBoard">
          <Provider store={this.store}>
          <ScoreBoard />
          </Provider>
          </div>
          <div className="GameBoard">
          <GameBoard store={this.store}/>
          </div>
        </div>
        <Provider store={this.store}>
        <SettingsControl />
        </Provider>
      </div>
    );
  }
}

export default App;


