import { Layer } from 'react-konva';
import * as React from 'react';

import { Player, PlayerState, Point } from '../../utils/objectTypes';
import { colorNumToName, layouter } from '../../utils/functions';
import PlayerComponent from './GameElements/PlayerVisual';
import TailComponent from './GameElements/TailVisual';

export interface Props {
  players: PlayerState,
  dimension: Point,
  optimized: boolean
}

class Players extends React.Component<Props, object>{

  playerState: PlayerState;
  dimension: Point;
  tailComponents: {[name: string]: JSX.Element[]};
  playerComponents: {[name: string]: JSX.Element};
  canvasDimension: Point;
  lastCoords: {[name: string]: Point};
  
 constructor(props: Props) {
    super(props)
    this.playerState = props.players;
    this.dimension = props.dimension;
    this.tailComponents = {};
    this.playerComponents = {};
    this.lastCoords = {};
    
    this.props.players.names.forEach(name => this.lastCoords[name] = {X: this.props.players.byName[name].coords.X, Y: this.props.players.byName[name].coords.Y});
  }
    
  componentDidMount() {
    let layer = this.refs.layer as any;
    let canvas = layer.canvas._canvas as HTMLCanvasElement;
    this.canvasDimension= {X:canvas.width,Y:canvas.height};
  }

  componentWillUpdate(nextProps: Props) {
    var names: string[] = playersThatHaveChanged(this.props, nextProps, this.lastCoords);

    names.forEach((name: string) => {
      var curPlayer: Player = this.props.players.byName[name];
      
      this.playerComponents[name] = (<PlayerComponent
        color={colorNumToName[curPlayer.color]}
        rect={layouter(this.dimension, this.canvasDimension, curPlayer.coords)}
        direction={curPlayer.direction}
        speed={curPlayer.speed}
        optimized={this.props.optimized}
        key={curPlayer.name}/>);
      
      if(curPlayer.tail.length === 0){
        this.tailComponents[name] = [];
      } else {
        this.tailComponents[name].push(<TailComponent
          color={colorNumToName[curPlayer.color]}
          rect={layouter(this.dimension, this.canvasDimension, curPlayer.tail[curPlayer.tail.length - 1])}
          key={curPlayer.name + curPlayer.tail.length.toString()}/>)
      }
    });
  }
    
    

  render(){
    return (
      <Layer ref='layer'>
      {Object.keys(this.playerComponents).map(k => this.playerComponents[k])}
      {Object.keys(this.tailComponents).map(k => this.tailComponents[k])}
      </Layer>
    );
  }
}

export default Players;

function playersThatHaveChanged(props: Props, nextProps: Props, lastCoords: any): string[]{
  var players: string[] = [];
  nextProps.players.names.forEach(name => {
    var oldPlayer = lastCoords[name];
    var newPlayer = nextProps.players.byName[name];
    nextProps;
    if(oldPlayer.X !== newPlayer.coords.X || oldPlayer.Y !== newPlayer.coords.Y){
      players.push(name);
      lastCoords[name] = {X: nextProps.players.byName[name].coords.X, Y: nextProps.players.byName[name].coords.Y}
    }
  });
  return players;
}



