import { ArrayHasCoords, colorNumToName, createHistogram } from '../utils/functions';
import { createBoard, setSettings } from '../ReduxStore/actionTypes';
import * as actions from '../ReduxStore/actionTypes';
import { Store } from 'react-redux';

import { BoardField, GameState, Player, PlayerState, Point } from '../utils/objectTypes';

export function registerGameController(store: Store<GameState>){
    
  function createCollisionDetector(width: number): {EnterFieldEvent: (point: Point) => void, RegisterOnField: (point: Point, playerName: string) => void, UnregisterOnField: (point: Point, playerName: string) => void}{
    var registeredPlayers: {[field: number]: string} = {};
    registeredPlayers;
    var EnterFieldEvent: (point: Point) => void = function(point: Point){};
    var RegisterOnField: (point: Point, playerName: string) => void = function(point: Point){};
    var UnregisterOnField: (point: Point, playerName: string) => void = function(point: Point){};
    return {EnterFieldEvent, RegisterOnField, UnregisterOnField};
  }
  
  var gameState: GameState = store.getState();
  
  // create board
  store.dispatch(createBoard(gameState.fieldState.dimension));  

  // set startTime and fire endgame timer
  store.dispatch(setSettings({...gameState.settings, startTime: new Date().getTime()}));
  setTimeout(endGame, gameState.settings.endTime);
  
  // set up collision detector
  var EnterFieldEvent: (point: Point) => void;
  var RegisterOnField: (point: Point, playerName: string) => void;
  var UnregisterOnField: (point: Point, playerName: string) => void = function(point: Point){};
  ({EnterFieldEvent, RegisterOnField, UnregisterOnField} = createCollisionDetector(gameState.fieldState.dimension.X));
  
  var playersState: PlayerState = gameState.playerState;
  
  // create key dispatcher and register it for keydown event
  var keyDispatcher: (event: any) => void = createDispatcher(playersState.byName);
  window.addEventListener("keydown", keyDispatcher, false);

  playersState.names.forEach((value, index) => {
    var curPlayer = playersState.byName[value];
    
    // set starting position
    requestAnimationFrame(function() {
      setStartingPosition(curPlayer, index, store);
    });

    // claim starting fields
    requestAnimationFrame(function() {
      claimStartingFields(value, store);
    });
    
    // fire game loop for this player
    setTimeout(updatePlayer, curPlayer.speed, value);
  });

  function updatePlayer(name: string) {
    
    var state: GameState = store.getState();
    var fields = state.fieldState.fields;
    var playerState: Player = {...state.playerState.byName[name]};

    // if player is on enemy territory spawn tail
    var playerField: BoardField = fields[playerState.coords.X][playerState.coords.Y];
    if(playerField.color !== playerState.color){      
      playerState.tail = playerState.tail.concat({...playerState.coords});

      // and register it for collision detector
      RegisterOnField(playerState.coords, playerState.name);
    }
    // move player
    playerState = movePlayer(playerState);
    // if he is out of bounds, kill him
    if (outOfBoard(playerState.coords, state.fieldState.dimension)){
      playerState = killPlayer(playerState);
      store.dispatch(actions.modifyPlayer(playerState));
      setTimeout(updatePlayer, playerState.deathPenalty, playerState.name);
      return;
    }

    // fire a fieldEntered event
    EnterFieldEvent(playerState.coords);
    
    // if player is on his own territory, claim fields and unregister from collision detector
    if(playerField.color === playerState.color){
      var claimedPoints: Point[] = claimTerritoryAlgorithm(playerState.tail, fields, playerState.color);
      store.dispatch(actions.claimFields(claimedPoints, playerState.color));
      
      playerState = killTail(playerState);
    }
    
    store.dispatch(actions.modifyPlayer(playerState));
    setTimeout(updatePlayer, playerState.speed, playerState.name);
  };

  function createDispatcher(map: {[name: string]: Player}): (event: any) => void{
    var keyCodesArr: {num: number, playerName: string, action: string}[] = [];
    var players: Player[] = Object.keys(map).map(n => map[n]);
    
    players.forEach((player: Player) => {
      var keyCodesDict: {[num: number]: string} = swap(player.keyMapping);
      Object.keys(keyCodesDict).forEach(num => {
        var number = parseInt(num);
        var element = {num: number, playerName: player.name, action: keyCodesDict[num]};
        keyCodesArr.push(element);
      });
    });

    return function(event: any){
      event.preventDefault();
      keyCodesArr.forEach(action => {
        if (event.keyCode === action.num){
          dispatchPlayerAction(action.playerName, action.action);
        }
      });
    };
  };

  function dispatchPlayerAction(playerName: string, action: string){
    var player: Player = store.getState().playerState.byName[playerName];

    switch(action){
      case 'up':
      if (player.direction !== 'up'){
        store.dispatch(actions.modifyPlayer({...player, direction: 'up'}));
      }
      break;
      case 'down':
      if (player.direction !== 'down'){
        store.dispatch(actions.modifyPlayer({...player, direction: 'down'}));
      }
      break;
      case 'left':
      if (player.direction !== 'left'){
        store.dispatch(actions.modifyPlayer({...player, direction: 'left'}));
      }
      break;
      case 'right':    
      if (player.direction !== 'right'){
        store.dispatch(actions.modifyPlayer({...player, direction: 'right'}));
      }
      break;
    }
  };

  function endGame(){
    document.addEventListener("keydown", function(e) {
      e.stopImmediatePropagation();
    }, false);

    var gameState: GameState = store.getState();
    var colorsArr: number[]; var valuesArr: number[];
    ({colorsArr, valuesArr} = createHistogram(gameState.fieldState.fields));
    var players = Object.keys(gameState.playerState.byName).map(n => gameState.playerState.byName[n]);
    var winner: Player | undefined = players.find(p => p.color === colorsArr[0]);
    writeEndGameMessage(winner)
  };

  function writeEndGameMessage(winner: Player | undefined){
    if (winner === undefined){
      return;
    };
    
    var color: string = colorNumToName[winner.color];

    var canv = document.getElementsByClassName('GameBoard')[0].querySelector('canvas');
    if (canv !== null){
      var co = canv.getContext('2d');
      if (co !== null){
        // Draw a red square
        co.fillStyle = 'white';
        co.globalAlpha = 0.5;
        co.fillRect(0,0,canv.width, canv.height);
        
        co.font = getFont(canv.width);
        co.globalAlpha = 1;
        co.fillStyle = color;
        co.textAlign="end";
        co.fillText(winner.name,canv.width/2, canv.height/2);
        co.fillStyle = 'black';
        co.textAlign='start';
        co.fillText(' wins!',canv.width/2, canv.height/2);
        
      }
    }
  }

  function killPlayer(playerState: Player): Player{
    playerState = killTail(playerState);
    return {...playerState, coords: {...playerState.startCoords}}
  }

  function killTail(playerState: Player): Player{
    
    playerState.tail.forEach((t) => {
      UnregisterOnField(t, playerState.name);
    });

    return {...playerState, tail: []};
  }
}

// Helpers

function getFont(canvasWidth: number) {
  var ratio = 1/15;   // calc ratio
  var size = canvasWidth * ratio;   // get font size based on current width
  return (size|0) + 'px sans-serif'; // set font
}

function setStartingPosition(curPlayer: Player, index: number, store: Store<GameState>){
    var dim: Point = store.getState().fieldState.dimension;
    var x: number = 0;
    var y: number = 0;

    switch (index){
      case 0:
        x = Math.floor(dim.X/4);
        y = Math.floor(dim.Y/4);
        break;
        case 1:
        x = Math.floor(dim.X/4*3);
        y = Math.floor(dim.Y/4*3);
        break;
        case 2:
        x = Math.floor(dim.X/4*3);
        y = Math.floor(dim.Y/4);
        break;
        case 3:
        x = Math.floor(dim.X/4);
        y = Math.floor(dim.Y/4*3);
        break;
        case 4:
        x = Math.floor(dim.X/2);
        y = Math.floor(dim.Y/4);
        break;
        case 5:
        x = Math.floor(dim.X/2);
        y = Math.floor(dim.Y/4*3);
        break;
        case 6:
        x = Math.floor(dim.X/4*3);
        y = Math.floor(dim.Y/2);
        break;
        case 7:
        x = Math.floor(dim.X/4);
        y = Math.floor(dim.Y/2);
        break;
      }
    curPlayer = {...curPlayer, startCoords: {X: x, Y: y}, coords: {X: x, Y: y}};
    store.dispatch(actions.modifyPlayer(curPlayer));
  }

function claimStartingFields(name: string, store: Store<GameState>){
  var curPlayer: Player = store.getState().playerState.byName[name];
  var radius: number = store.getState().fieldState.startingTerritorySize;
  var point = curPlayer.coords;
  var coords: Point[] = [];
  for(var x = -radius; x <= radius; ++x){
    for(var y = -radius; y <= radius; ++y){
      if(x*x + y*y <= radius* radius){
        coords.push({X: x + point.X, Y: y + point.Y});
      }
    }
  }

  store.dispatch(actions.claimFields(coords, curPlayer.color));
}

function movePlayer(player: Player): Player{
  player.coords = {...player.coords};

  switch(player.direction){
    case 'up':
      player.coords.Y -= 1;
      break;
    case 'down':
      player.coords.Y += 1;
      break;
    case 'left':
      player.coords.X -= 1;
      break;
    case 'right':
      player.coords.X += 1;
      break;
    default: break;
  }
  return player;
}
  
function outOfBoard(coords: Point, dimension: Point): boolean {
  return coords.X < 0 || coords.Y < 0 || coords.X >= dimension.X || coords.Y >= dimension.Y;
}

function swap(json: any): any{
  var ret = {};
  for(var key in json){
    ret[json[key]] = key;
  }
  return ret;
};

function claimTerritoryAlgorithm(tail: Point[], fields: BoardField[][], color: number): Point[]{
  if(tail.length === 0){
    return [];
  }

  var dimension: Point = {X: fields.length, Y: fields[0].length};
  var tailNeighbours: Point[] = [];
  var outerNeighbours: Point[] = [];
  var claimedFields: Point[] = [];
  var returnFields: Point[] = [];  

  fields.forEach((value, index) => {
    var row = index;
    value.forEach((value, index) => {
      if (value.color === color){
        claimedFields.push({X: row, Y: index});
      }
    });
  });
  
  tail.forEach((point: Point) => {
    claimedFields.push(point);
    returnFields.push(point);
    getNeighbors(point, dimension).forEach((n: Point) => {
      if(!ArrayHasCoords(n, claimedFields) && !ArrayHasCoords(n, tailNeighbours))
      {
        tailNeighbours.push(n);
      }
    });
  });
  tailNeighbours.forEach((n: Point) => {
      // check for performance boost (won't start algorithm for fields already classified as outer or taken,
      // so most of the cases algorithm will run only twice)
      if(!ArrayHasCoords(n, claimedFields) && !ArrayHasCoords(n, outerNeighbours))
      {
          // this is one area of adjecent, not taken fields
          var joinedArea: Point[] = [];
          // algorithm returns bool value indicating if any of the fields in joined area aren't at the edge of a map
          var joinedAreaIsInside: boolean = ClaimTerritoryRecursive(n, outerNeighbours, joinedArea, claimedFields, dimension);
          if(joinedAreaIsInside)
          {
              // if so, joined area is inside players' terrain
              claimedFields = claimedFields.concat(joinedArea.concat(tail));
              returnFields = returnFields.concat(joinedArea.concat(tail));
          }
      }
  });
  return returnFields;
};

function ClaimTerritoryRecursive(currentField: Point, outerNeighbours: Point[], joinedArea: Point[], claimedFields: Point[], dimension: Point): boolean{
  
  joinedArea.push(currentField);
  
  var currentNeighbors: Point[] = getNeighbors(currentField, dimension);
  if(currentNeighbors.length !== 4)
  {
      outerNeighbours.push.apply(joinedArea);
      return false;
  }
  //For each of neighbours that arent in joined area list and does not belong to player make recurrent call
  var eachNeighbourReturnsTrue: boolean = true;

  currentNeighbors.forEach((nn: Point) =>{
    if (!eachNeighbourReturnsTrue){
      return;
    };    
    if((!ArrayHasCoords(nn, claimedFields)) && (!ArrayHasCoords(nn, joinedArea)))
    {
        if(!ClaimTerritoryRecursive(nn, outerNeighbours, joinedArea, claimedFields, dimension))
        {
            eachNeighbourReturnsTrue = false;
        }
    }
  });
  return eachNeighbourReturnsTrue;
};

function getNeighbors(p: Point, d: Point): Point[]{
  var neighbors: Point[] = [
    {X: p.X, Y: p.Y-1},
    {X: p.X, Y: p.Y+1},
    {X: p.X+1, Y: p.Y},
    {X: p.X-1, Y: p.Y},
  ].filter(point => !outOfBoard(point, d));
  return neighbors;
}


