import { Store } from 'redux';
import * as React from 'react';
import {Stage} from 'react-konva';

import { GameState, Point } from '../../utils/objectTypes';
import Players from '../../ReduxStore/containers/Players';
import Fields from '../../ReduxStore/containers/Fields';
import { Provider } from 'react-redux';
import { getCanvasDimension } from '../../utils/functions';

export interface Props {
  store: Store<GameState>
}

function GameBoard(props: Props) {
  const dim: Point = props.store.getState().fieldState.dimension;
  var canvasDimension: Point = getCanvasDimension(dim);
    return (
      <div className="App">
        <Stage width={canvasDimension.X} height={canvasDimension.Y} >
          <Provider store={props.store}>
            <Fields />
          </Provider>
          <Provider store={props.store}>
            <Players />
          </Provider>
      </Stage>
      </div>
    );
}

export default GameBoard;


