import { FastLayer } from 'react-konva';
import * as React from 'react';

import { BoardField, FieldState, Point } from '../../utils/objectTypes';
import { colorNumToName, layouter } from '../../utils/functions';
import { Component } from 'react';

class Fields extends Component<FieldState, object>{
  
  canvasDimension: Point;
  canvas: HTMLCanvasElement;
  ctx: CanvasRenderingContext2D;
  lastUpdatedCoords: Point[];
  
  constructor(props: FieldState){
    super(props);
    
    this.lastUpdatedCoords = [];
  }

  componentDidMount() {
    let layer = this.refs.layer as any;    
    this.canvas = layer.canvas._canvas as any;
    this.ctx = this.canvas.getContext('2d') as CanvasRenderingContext2D;
    this.canvasDimension = {X:this.canvas.width,Y:this.canvas.height};
        
  }

  componentWillReceiveProps(nextProps: FieldState) {
    nextProps.lastUpdatedCoords.forEach((p: Point) =>
    this.lastUpdatedCoords.push(p)
  );
  }

  render(){
    return (
      <FastLayer ref='layer'>
      </FastLayer>
    );
}

  componentDidUpdate(){
    let fields = this.props.fields;
    let color: number = fields[this.props.lastUpdatedCoords[0].X][this.props.lastUpdatedCoords[0].Y].color;

    this.ctx.shadowColor = 'grey';
    this.ctx.shadowBlur = 2;
    this.ctx.globalAlpha = 0.7;    
    this.ctx.fillStyle = colorNumToName[color];
    
    this.ctx.beginPath();    
    this.props.lastUpdatedCoords.forEach(coord => this.drawRectangle(coord, fields[coord.X][coord.Y], this.ctx));
    this.ctx.fill();
    this.ctx.closePath();
    this.ctx.globalAlpha = 1;
    this.lastUpdatedCoords = [];
  }

  drawRectangle(fieldLocation: Point, field: BoardField, ctx: CanvasRenderingContext2D) {    
    var X, Y, Width, Height;
    ({X, Y, Width, Height} = layouter(this.props.dimension, this.canvasDimension, fieldLocation)); 
    
    ctx.rect(X, Y, Width, Height);
  }

}

export default Fields;



